@import url('https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap');


$container-size: 610px;
$center-position: 0 auto;
$basic-font-size: 14px;
$font-family: 'Inter', sans-serif;
$link-decoration: 'none';
$link-color: #007bff;
$flex: flex;

$comments-container-size: 480px;
// information
.info-page__container {
 width: $container-size;
 max-width: $container-size;
 margin:$center-position;

}

.info-page__paragraph {
 font-family: $font-family;
 font-size: $basic-font-size;
 line-height: 163%;
}

.info-page__link {
 text-decoration: $link-decoration;
 color: $link-color;
}

.info-page__btn {
 width: 550px;
 height: 65px;
 display: $flex !important;
 background: linear-gradient(0deg, #EEF0F4, #EEF0F4), #FFFFFF;
border: 1px dashed #77A193;
box-sizing: border-box;
border-radius: 10px;
align-items: center;
}
.info-page__btn__plus {
 font-size: 35px !important;
}
// comments
.comment-list__container {
 width: $comments-container-size;
 max-width: $comments-container-size;
 margin:$center-position;
}
.comment-list__header {
 font-family: $font-family;
 display: $flex;
 justify-content: space-between;
}
.comment-list__body {
 height: 462px;
 max-height: 462px;
}

.comment-list__input-futter {
  background: #EEF0F4;
 }
.comment-list__input-futter {
 width: 100%;
 max-width: 480px;
 display: $flex;
 justify-content: center;
 padding:  20px 0px 20px 0px;

}
 .comment-list__textarea {
  padding-top: 12px;
  width: 420px;
  height: 72px;
  padding-left: 15px;
  border-radius: 10px;
  border: none;
 }
 input::placeholder {
  font-size:12px;
  text-align: start;
 }

 .comment-list__input-image {
   position: absolute;
   right: 24%;
   padding-top: 12px;
 }

